import React, {useState, useEffect} from 'react';
import {connect} from 'react-redux';
import HeroComponents from "components/layout-components/CustomrPageHeaderAlt";
import ContainerTailwind from "components/layout-components/ContainerTailwind";
import {
	Button,
	Tooltip,
	Card,
	Col,
	DatePicker,
	Drawer,
	Form,
	Input,
	message,
	Modal,
	Row,
	Select,
	Table,
	Divider
} from "antd";
import {
	DeleteOutlined,
	EditOutlined, PlusOutlined,
	ReloadOutlined, SettingOutlined
} from "@ant-design/icons";
import {useHistory} from 'react-router-dom'
import ApiService from "services/api.service";
import moment from "moment/moment";
import ButtonPermission from "../../../../../../components/permission-components/button.permission";

function mapStateToProps(state) {
	return {};
}

function Index() {
	
	const history = useHistory();
	
	const defaultColumns = [
		{
			visible: true,
			key: "no",
			title: "No",
			dataIndex: "id",
			width: 70,
			render: (_, val, index) => ((index + 1) + (params?.limit * (params?.page - 1)))
		},
		{
			visible: true,
			key: "name",
			title: "Name",
			dataIndex: ['data','name']
		},
		{
			visible: true,
			key: "code",
			title: "Code",
			dataIndex: ['data','code'],
		},
		{
			visible: false,
			key: 'action',
			title: "Action",
			width: 120,
			dataIndex: 'id',
			render: (_, val, index) => {
				return (
					<div className={'text-right d-flex !mk-gap-2 justify-content-end'}>
						<Button size={'small'} type={'primary'} ghost icon={<EditOutlined/>}
						        onClick={() => handleClickEdit(val, index)}/>
						<Button
							size={'small'}
							type={'primary'}
							danger
							ghost
							disabled={loadings.length > 0 ? true : _?.isDeleted ? true : data.some((item) => {
								return typeof (item?.isDeleted) !== 'undefined' ? item?.isDeleted === true : false
							})}
							onClick={() => {
								handleDeleteRow(_, index)
							}}
							loading={loadings[index] ?? false}
							icon={<DeleteOutlined/>}/>
					</div>
				)
			}
		},
	];
	const [selectedColumns,setSelectedColumn] = useState(()=> defaultColumns.map((item)=> ({key:item.key})))
	const [loading, setLoading] = useState(true)
	const [params, setParams] = useState({
		page: 1,
		limit: 10
	})
	const [pagination, setPagination] = useState({
		page: 1,
		limit: 10
	})
	const [data, setData] = useState([])
	
	const [loadings, setLoadings] = useState([])
	const [visibleDrawer, setVisibleDrawer] = useState(false)
	const [visible, setVisible] = useState(false)
	const [selected, setSelected] = useState(null)
	const [form] = Form.useForm();
	
	function _caller() {
		setLoading(true)
		new ApiService({
			url: "/api/v1/master-data/airline-status",
			config: {
				params: {
					...params
				}
			}
		})
			.get()
			.then((response) => {
				response.data = response.data.map((item) => ({
					...item,
					isDeleted:false,
					schedule_time: moment(item?.schedule_time, 'HH:mm').isValid() ? moment(item?.schedule_time, 'HH:mm') : item?.schedule_time
				}))
				setData([...response?.data])
				setPagination({
					...pagination,
					...response?.pagination,
					...response?.paginate
				})
				setLoading(false)
			})
			.catch((err) => {
				setData([])
				setLoading(false)
			})
	}
	
	
	useEffect(() => {
		_caller()
	}, [params])
	
	useEffect(() => {
		form.resetFields()
	}, [visible])
	
	const enterLoading = (index, value = true) => {
		setLoadings((prevLoadings) => {
			prevLoadings[index] = value;
			return prevLoadings ?? []
		});
	};
	
	const onFinish = () => {
		form.validateFields()
			.then((value) => {
				Reflect.set(value, 'schedule_time', moment(value?.schedule_time).isValid() ? moment(value?.schedule_time).format("HH:mm") : value?.schedule_time)
				new ApiService({
					body: value,
					url: `/api/v1/master-data/airline-status`,
					mode:"express"
				}).post()
					.then((response) => {
						message.success({
							content: response?.message ?? "Successfully created",
							key: "create-city",
							duration: 2
						})
						setLoading(false)
					})
					.catch((err) => {
						message.info(JSON.stringify(err))
						message.error({
							content: err?.message ?? "Error: Some Error",
							key: "create-city",
							duration: 2
						})
						setLoading(false)
					})
			})
			.catch((err) => {
			
			})
	}
	
	function handleClickEdit(_, index) {
		
		setVisible(!visible)
		let newField = _ ?? {}
		form.resetFields()
		Object.freeze(newField)
		setSelected(newField)
		
	}
	
	function onCloseModal() {
		form.resetFields()
		setVisible(false)
		setSelected(null)
		
	}
	
	
	function handleDeleteRow(_, index) {
		let indexed = data.findIndex((item)=> item?.id === _)
		enterLoading(indexed, true)
		new ApiService({
			url: `/api/v1/master-data/${_}`,
			mode:"express"
		}).delete()
			.then((response) => {
				if (!response?.error) {

					if(indexed >= 0){
						data[indexed].isDeleted = true
						setData([...data])
						enterLoading(indexed,false)
					}
					let newData = data.filter((item) => item.id !== _)
					console.log({newData,data,_})
					message.success(response?.message ?? "Successfully deleted")
					setTimeout(() => {
						setData([...newData])
						setLoadings([])
					}, 2000)
				} else {
					enterLoading(index, false)
					setLoadings([])
					message.error(response?.message ?? "Error: delete by id " + _)
				}
				
			})
			.catch((err) => {
				enterLoading(index, false)
				setLoadings([])
			})
	}
	
	const onChangeDrawer = (val) => {
		setVisibleDrawer(val ?? false)
	}
	
	
	const extra = (
		<div className={'d-flex align-items-center mk-gap-2'}>
			<Tooltip title={'Reload Data'}>
				<Button icon={<ReloadOutlined/>} ghost type={'primary'} onClick={_caller} loading={loading} size={'small'}/>
			</Tooltip>
		</div>
	)
	
	const [columns, setColumns] = useState(()=> defaultColumns ?? [])
	
	const onColumnsChange = (value) => {
		value =  value.map((item)=> ({key:item}))
		setSelectedColumn(value)
	}
	return (
		<React.Fragment>
			<Drawer
				title={`Filter`}
				placement={"right"}
				closable
				onClose={() => onChangeDrawer(false)}
				visible={visibleDrawer}
			>
				<div className={'w-100'}>
					<h5>Table Column</h5>
					<Select onChange={onColumnsChange} mode={'tags'} className={'w-100'} placeholder={'Select Columns Active'}
					        defaultValue={defaultColumns.map((item) => item?.key) ?? []}>
						{
							defaultColumns.map((item) => (
								<Select.Option value={item.key} key={item.key}>{`${item.key}`.replace(/_/g, ' ')}</Select.Option>
							))
						}
					</Select>
					
					<Divider/>
				</div>
			</Drawer>
			
			<Modal
				style={{
					width: "520px !important",
				}}
				title={'Edit'}
				visible={visible} onOk={onFinish} onCancel={onCloseModal}>
				<Form form={form} initialValues={{
					...selected,
				}} layout={'vertical'}>
					<Form.Item name={"identifier"} label={'Identifier'} hidden>
						<Input/>
					</Form.Item>
					<Form.Item name={['data', 'code']} label={'Code'} rules={[{required: true, message: "Can't be empty!"}]}>
						<Input/>
					</Form.Item>
					<Form.Item name={['data', 'name']} label={'Display Name'} rules={[{required: true, message: "Can't be empty!"}]}>
						<Input/>
					</Form.Item>
				</Form>
			</Modal>
			
			
			<HeroComponents
				loading={false}
				name={"Overview"}
				positionName={['Master Data', 'Airlines','Flight Status']}
				thumbnail={'/overview.png'}
				extra={<>
					<ButtonPermission
						permission={['public','admin']}
						theme={{
							type:"disabled",
							property:{
								message:"Access Denied"
							}
						}}
						config={{
							onClick: function(){
								history.push('/dashboard/master-data/airlines/flight-status/create')
							},
							ghost:true,
							icon:<PlusOutlined/>,
							type:"primary",
							size:"small"
						}}>
						Add New
					</ButtonPermission>
				</>}
			/>
			
			<div style={{marginTop: 90}}>
				<ContainerTailwind>
					<Card bordered={false} extra={extra}>
						<Table
							loading={loading}
							dataSource={data}
							rowClassName={(record, index) => record?.isDeleted ? "!mk-bg-red-500 !mk-bg-opacity-20" : ''}
							columns={defaultColumns.filter((item)=> selectedColumns.filter((child)=> child?.key === item.key).length > 0) ?? []}
							pagination={{
								pageSize: params?.limit,
								current: params?.page,
								total: pagination?.total_items ?? 0,
								onChange: function (page, pageSize) {
									setParams({
										...params,
										page,
										limit: pageSize
									})
								}
							}}
						/>
					</Card>
				</ContainerTailwind>
			</div>
		</React.Fragment>
	);
}

export default connect(
	mapStateToProps,
)(Index);